import React,{ Component } from "react";
import { Link } from "react-router-dom";
import axios from 'axios';
import DefaultImage from '../resources/NoNoteImage.png';
import urlConst from '../config/config';
import Background from '../resources/dashboard_image.jpg';

export default class RegistrationPage extends Component{
    state={
        firstname: '',
        lastname: '',
        email:'',
        mobileNumber:'',
        password:'',
        profilePic:'',
        users : [],
        file:'',
        validation:{
            firstname: null,
            lastname: null,
            email: null,
            mobileNumber: null,
            password: null,
            profilePic: null
        }
    };

  constructor(props : any){
    super(props);
     
    this.formInputChange= this.formInputChange.bind(this);
    this.formSubmit= this.formSubmit.bind(this);
    this.onImageChange = this.onImageChange.bind(this);
  }   

 onImageChange = (event : any)=> {
    if (event.target.files && event.target.files[0]) {
      let img = event.target.files[0];
      this.setState({
          profilePic: URL.createObjectURL(img),
          file:  img
      });
    }
  };
  
  componentDidMount(){
    let user_id=localStorage.getItem('token');
    if(user_id!==''){
        axios.get(urlConst.url +`/useredit`,
         { headers: {'authorization': `${localStorage.token}`}}
        )
        .then(resp =>{      
            console.log(resp.data[0]);      
            this.setState({
                firstname:resp.data[0].firstname,
                lastname: resp.data[0].lastname,
                email: resp.data[0].email,
                mobileNumber: resp.data[0].mobileNumber,
                password: resp.data[0].password,
                profilePic: resp.data[0].profilePic
            });
        }); 
    }

  }
 formInputChange(event : any){
    this.setState({
        [event.target.name] : event.target.value
    });

    const validation: any = this.state.validation;
    if(!event.target.value){
        validation[event.target.name]='Can\'t be empty';
    }
    else{
        validation[event.target.name]=null;
    }

    this.setState({
        validation
    });
 }  
 
 formSubmit(event : any){
     event.preventDefault();
     const formData = new FormData();
     formData.append('file', this.state.file);     
     axios.post(urlConst.url +"/users/image-upload", formData,
     { headers: { 'authorization': `${localStorage.token}` } }
     )
         .then((response) => {
             alert("The file is successfully uploaded");
         }).catch((error) => {
         });

 }
    render(){
        return(
            <div style={{ backgroundColor: "black", backgroundImage: `url(${Background})`, height: "657px", backgroundSize: 'cover', backgroundAttachment:'fixed' }}>
            <br></br>
            <div className='container col-3'>
            <br></br>
                <div>   
                <h3 style={{ color: "#ffe20f",position: 'fixed', left: '50px' }}>Profile Info:</h3>    
                <Link to="/dashboard" className='btn btn-outline-primary' style={{position: 'fixed',right: '130px'}}>Go to Dashboard</Link>
                <Link to="/" className='btn btn-danger' style={{position: 'fixed',right: '34px'}}>Sign Out</Link>                
                </div>
                <br/><br/><br/>
                <form onSubmit={this.formSubmit}>                
                    <div className='form-row mb-3'>
                        <input type='text' name='firstname' placeholder='First Name' className='form-control color' value={this.state.firstname}   
                       disabled={true}/>
                    </div>
                    <div className='form-row mb-3'>
                        <input type='text' name='lastname' placeholder='Last Name' className='form-control' value={this.state.lastname} 
                         disabled={true}/>
                    </div>
                    <div className='form-row mb-3'>
                        <input type='text' name='email' placeholder='Email' className='form-control' value={this.state.email} 
                         disabled={true}/>
                    </div>
                    <div className='form-row mb-3'>
                        <input type='text' name='mobileNumber' placeholder='Mobile Number' className='form-control' value={this.state.mobileNumber}
                         disabled={true}/>
                    </div>
                    <div className='form-row mb-3'>      
                        {this.state.profilePic === ''?
                        <img src={DefaultImage}  style={{height:140, width:175, float:'left'}} />
                        :<img src={this.state.profilePic} style={{height:140, width:175, float:'left'}} />}    
                        {/* <img src={require('./uploads/e007da5c-bcc5-42d6-ad5e-04a46bffc20d.jpg')}  style={{height:140, width:175, float:'left'}} /> */}
                        <input type="file" name="myImage" style={{ width: 100, float: 'right', marginLeft: 40, marginTop: 111 }}  onChange={this.onImageChange} />                   
                    </div>
                   
                    <div className='form-row mb-3'>
                        <button type='submit' className='btn btn-success'>Confirm</button>
                        <Link to="/dashboard" className='btn btn-outline-danger ml-5' >Cancel</Link>
                    </div>
                </form>                
             </div>
            </div>
        )
    }
}